import axios from "axios"

async function getTokenInfo({ isTrc10, tokenIdOrAddress }) {
    let returnInfo = {
      imgsrc: "https://via.placeholder.com/100",
      name: "NoName"
    };
    let apiLink =
      (isTrc10
        ? "https://apilist.tronscan.org/api/token?id="
        : "https://apilist.tronscan.org/api/token_trc20?contract=") +
      tokenIdOrAddress;
    let response = await axios.get(apiLink);
    if (
      isTrc10 &&
      response.data.data[0].imgUrl != undefined &&
      response.data.data[0].abbr != undefined
    ) {
      returnInfo.imgsrc = response.data.data[0].imgUrl;
      returnInfo.name = response.data.data[0].abbr;
    }
    if (
      !isTrc10 &&
      response.data.trc20_tokens != undefined &&
      response.data.trc20_tokens.length > 0 &&
      response.data.trc20_tokens[0].icon_url != undefined
    ) {
      returnInfo.imgsrc = response.data.trc20_tokens[0].icon_url;
      returnInfo.name = response.data.trc20_tokens[0].symbol;
    }
    return returnInfo;
  }

  export {getTokenInfo}