import Vue from "vue";
import Vuex from "vuex";
import contract from "@/Utils/Contract.js";
// import { Toast } from 'buefy/dist/components/toast'
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    contractAddress: "TVZRcGukurRbzN2PvjdcnnFVSdfsY1kej6", //"TXjzswUfKGgxf72ZHevci2n3XY9CnGVfj5",
    myAddress: "",
    loadingStatus:""
  },
  mutations: {
    SET_MYADDRESS(state, payload) {
      state.myAddress = payload;
    },
    SET_LOADING_STATUS(state, status){
      state.loadingStatus = status;
    }
  },
  actions: {
    setMyAdress(context, payload) {
      context.commit("SET_MYADDRESS", payload);
    },
    async loadDataFromContract(context) {
      await contract.loadContract(context.state.contractAddress);
      context.commit("SET_LOADING_STATUS", "Load elements");
      await contract.loadElements();
      await contract.loadSpeedUps();
      context.commit("SET_LOADING_STATUS", "Get user balance");
      await contract.loadBalances(context.state.myAddress);
      context.commit("SET_LOADING_STATUS", "Load factories's data");
      await contract.loadFactories();
      context.commit("SET_LOADING_STATUS", "Load user's plans");
      await contract.loadPlans(context.state.myAddress);
    },
  }
});
